import { FC, useContext, useEffect, useRef, useState } from 'react'
import { printDebounce, PrintDebounceRef } from '../../utils/utils'
import { TextBox } from './text-box'
import { MainContext } from '../App'
import './genshin.css'
import { Howl } from 'howler'

const textStates = [
  'Вот и стукнуло тебе 18, дорогуша. Совершеннолетие пришло, взрослая жизнь началась, родителям за сигами уже бегаешь :D, старость уже не за горами D:',
  'Я всем рассказывал, какой у меня резкий переход был по окончанию школы: только ЗНО закончилось, а уже сразу на full-time работу. Думал, что я “уникальный” в этом плане. Как же я ошибался…. ',
  [
    `С твоими американо-польскими горками это даже рядом не стоит. Такой ПАРКУР еще нужно постараться переплюнуть. Ровно на свое 18-ти летие ты умудрился:`,
    `- Съехать от родителей`,
    `- Сменить страну проживания`,
    `- Найти себе в новой стране квартиру (хоть и временно пока)`,
  ],
  'Осталось только найти 1000$+ Senior CSS Architect full-time job, девушку которая полюбит всем сердцем и все в ажуре, братан. Дальше только гринд, только качалка, только “YEAH BUDDY! LIGHTWEIGHT!"',
  'По поводу последнего - как там дела с Олей? Что-то планируешь делать в связи с внезапной и крайне радикальной сменой места жительства?',
  '“Senior CSS Architect” хоть и выдуманная позиция, но сути это не меняет - ты уже зашел в мир WEB-а с React-ом, назад пути нет, нужно двигаться дальше. У тебя как раз появиться свободное время, как только устаканишься в Польше, так что не вздумай его проебать - учи TS, учи React и будем пытаться пропихнуть тебя как Trainee/Junior куда-то.',
  'Первое время, конечно, прийдеться в Маке поработать - но это уже мелочи жизни :D',
]

const YeahBuddy = new Howl({
  src: 'music/yeah-buddy.mp3',
  preload: true,
  loop: false,
})

export const Genshin: FC = () => {
  const printRef = useRef<PrintDebounceRef>(null)
  const { bgVisible, setBgVisible, setGlobalStep } = useContext(MainContext)
  const [state, setState] = useState(0)
  const [visible, setVisible] = useState(false)
  const [parkourVisible, setParkourVisible] = useState(false)
  const [continueVisible, setContinueVisible] = useState(false)
  const [text, setText] = useState<string[]>([])

  useEffect(() => {
    setTimeout(() => setVisible(true), 4 * 1000)
  })

  useEffect(() => {
    if (visible && state <= textStates.length) {
      const [start, stop] = printDebounce(
        textStates[state],
        (char, _, i) => {
          if (state === 2 && char === 'П') setParkourVisible(true)
          if (state === 3 && char === 'Y') YeahBuddy.play()

          setText((prevText) => {
            if (!prevText[i]) prevText[i] = ''
            prevText[i] += char
            return [...prevText]
          })
        },
        { ref: printRef },
      )

      start().then((stoppedManually) => {
        if (!stoppedManually) setContinueVisible(true)
      })

      return () => {
        setParkourVisible(false)
        stop()
        setText([])
      }
    }
  }, [state, visible])

  const handleContinue = () => {
    const { isEnded, skip } = printRef.current ?? {}

    if (!isEnded) return skip?.()
    if (state === textStates.length - 1) return setGlobalStep(2)
    setContinueVisible(false)
    setState(state + 1)
  }
  return (
    <div
      className={`genshin-wrapper ${bgVisible && visible ? '' : 'opacity0'}`}
      onClick={handleContinue}
    >
      <TextBox text={text} />
      {parkourVisible && (
        <img
          alt=""
          src="img/parkour.webp"
          style={{
            width: '20%',
            position: 'absolute',
            top: '50%',
            right: '2%',
            transform: 'translateY(-50%)',
          }}
        />
      )}
      <button
        className={`genshin-button ${continueVisible ? '' : 'opacity0'}`}
        onClick={handleContinue}
      >
        Продолжить
      </button>
    </div>
  )
}
