import { FC, useContext, useEffect, useRef, useState } from 'react'
import cx from 'classnames'

import { printDebounce, PrintDebounceRef, sleep } from '../../utils/utils'
import { TextBox } from './text-box'
import { MainContext } from '../App'
import './final.css'

const textStates = [
  [
    `“Будущее - это тебе не прошлое, потому что сегодня уже не завтра, а завтра еще не вчера”.`,
    `Желаю всего наилучшего тебе, дружище. Перед тобой открылся новый мир с новыми возможностями и приключениями на свою задницу - так не проеби же свой билет на эти американо-польские горки! И себя не потеряй по пути - это очень важно.`,
  ],
  `PS. Я все конечно понимаю, но можно, вот прям пожалуйста, поменьше “Фактора Внезапности”? Типа, это ломает планы так-то - кухня это тебе не шутки! И геноцид - не обязательно делать геноцид всего и вся. То что камень - неживой предмет, а пол сделан из него же, не означает что на него не распространяется это правило. В следующий раз хоть предупреждай…`,
]

export const Final: FC = () => {
  const printRef = useRef<PrintDebounceRef>(null)
  const { bgVisible, setBgAudio } = useContext(MainContext)
  const [state, setState] = useState(0)
  const [visible, setVisible] = useState(false)
  const [continueVisible, setContinueVisible] = useState(false)
  const [shrekVisible, setShrekVisible] = useState(false)
  const [text, setText] = useState<string[]>([])

  useEffect(() => {
    setTimeout(() => setVisible(true), 1000)
  }, [])

  useEffect(() => {
    if (visible) {
      const [start, stop] = printDebounce(
        textStates[state],
        (char, _, i) => {
          setText((prevText) => {
            if (!prevText[i]) prevText[i] = ''
            prevText[i] += char
            return [...prevText]
          })
        },
        { ref: printRef },
      )

      start().then((stoppedManually) => {
        if (!stoppedManually) setContinueVisible(true)
      })

      return () => {
        stop()
        setText([])
      }
    }
  }, [setBgAudio, state, visible])

  useEffect(() => {
    if (state === 2) {
      setBgAudio(0)
      sleep(500).then(() => setShrekVisible(true))
    }
  }, [setBgAudio, state])

  const handleContinue = () => {
    const { isEnded, skip } = printRef.current ?? {}
    if (!isEnded) return skip?.()

    setContinueVisible(false)
    setState(state + 1)
  }

  return (
    <>
      <div
        className={cx(
          'assassins-wrapper',
          (bgVisible && visible) || 'opacity0',
        )}
        onClick={handleContinue}
      >
        <TextBox text={text} />
        <button
          className={`genshin-button ${continueVisible ? '' : 'opacity0'}`}
          onClick={handleContinue}
        >
          Продолжить
        </button>
      </div>
      {shrekVisible && (
        <video
          src="video/shrek_meme.mp4"
          style={{
            position: 'absolute',
            width: '100%',
            top: 0,
            left: 0,
          }}
          autoPlay
        />
      )}
    </>
  )
}
